@media only screen and (max-width: 1680px) {
    .navbar-container ul {
        padding-top: 1rem;
    }

    .navbar-container {
        width: 260px;
    }
    .navbar-container li {
        padding: 10px 12px;
    }

    .icon {
        font-size: 20px;
        padding-right: 18px;
    }
}